.experiance{
    display: flex;
    align-items: center;
    justify-content: center;
    gap:5rem;
    height: 30vh;
    margin-bottom: 8rem;
}
.achievement{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.circle{
    width: 4rem;
    height: 4rem;
    background: white;
    border-radius: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    font-weight: bold;
}
.circle::before{
    border-radius: 100%;
    content: "";
    background-image: linear-gradient(to bottom, #87e6fb 0%, orange 100%);
    top:-8px;
    left:-8px;
    bottom:-8px;
    right:-8px;
    z-index: -1;
    position: absolute;
    box-shadow: var(--boxShadow);
    
}
.achievement span{
    margin-top: 10px;
}

.achievement span:nth-of-type(2){
    color: orange;
}