

.t-wrapper{
  padding: 0 3rem 0 3rem;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: column;
  margin-top: 7rem;
  flex-direction: row;
  gap:2rem;
  position: relative;
  cursor: pointer;
}

.t-heading{
    align-self:start;
}

.t-heading>*{
    font-size: 2rem;
    font-weight: bold;
}
.t-heading span:nth-of-type(2){
    color:orange;
}

.t-blur1{
    left:38rem;
    top:16rem;
}
.t-blur2{
    top:9rem;
    left:0;
}
.testimonial{
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    padding: 2rem;
    width:30rem;
    height: 12rem;
    background: rgba(255,255,255,0.26);
    border: 7px solid var(--blurCard);
    box-shadow: var(--boxShadow);
    border-radius: 20px;
}
.testimonial>span{
    width: 6rem;
    height: 6rem;
    border-radius: 100%;
}
.testimonial>span{
    color:var(--grey);
}

.t-wrapper .swiper{
    width: 100%;
    height: 70%;
}
.swiper{
    display: flex;
}

.t-wrapper .swiper-slide{
    display: flex;
    align-items: center;
    justify-content: center;
}

