 .footer{
    display: flex;
    align-items: center;
    margin: -0.5rem -3.5rem;
    position: relative;
    margin-top: -7rem;
}
.f-content{
    position: absolute;
    display:flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    margin-top: 10rem;
    width: 100%;
    gap:4rem;
    color: white;
}
.f-icons{
    display: flex;
    gap:2rem;
    margin-top: 4rem;
} 


.nav-link-contact h4 {
    color: var(--text-color);
    margin: 18px 0;
    font-size: 20px;
    font-weight: 600;
  }
  
  #contact {
    background: #202020;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    gap: 2.5rem;
  }
  
  .nav-link-contact h2 {
    font-size: var(--h2-font);
    line-height: 1;
  }
  .list {
    margin-bottom: 2.8rem;
  }
  .list li {
    margin-bottom: 12px;
  }
  .list li a {
    display: block;
    color: var(--second-color);
    font-size: 14px;
    transition: all 0.45s ease;
  }
  
  .nav-link-contact p {
    font-size: var(--p-font);
    color: var(--second-color);
    line-height: 1.8;
    margin-bottom: 30px;
  }
  .list li a:hover {
    color: var(--text-color);
    transform: translateX(5px);
  }
  

.contact-icons i {
    height: 45px;
    width: 45px;
    background: var(--main-color);
    color: var(--text-color);
    font-size: 22px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border-radius: 8px;
    margin-right: 15px;
    transition: all 0.45s ease;
  }
  .contact-icons i:hover {
    background-color: var(--text-color);
    color: var(--main-color);
  }
  .contact-form form input,
  form textarea {
    width: 100%;
    padding: 14px;
    background: var(--bg-color);
    color: var(--text-color);
    border: none;
    outline: none;
    font-size: 15px;
    border-radius: 8px;
    margin-bottom: 10px;
  }
  .contact-form form {
    position: relative;
  }
  .contact-form textarea {
    resize: none;
    height: 240px;
  }
  .contact-form .submit {
    display: inline-block;
    font-size: 16px;
    background: var(--main-color);
    color: var(--text-color);
    width: 160px;
    transition: all 0.45s ease;
  }
  .contact-form .submit:hover {
    transform: scale(1.1);
    cursor: pointer;
  }